body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Main Styles */

* {
   font-family: 'Montserrat', sans-serif;
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   
   /* Primary Color */ 
   --bYel: #f9da66;
   --dBrown: #252525;
}

.container {
   width: 80%
}
/* Header Styles */

.Header {
    width: 100%;
    height: 100vh;
    background: url(/tyronevalmores/static/media/bg.244e383e.jpg);
    background-size: cover;
    background-attachment: fixed;
    background-origin: center center;
    background-repeat: no-repeat;
    position: relative;
}
.jumbotron {
    width: 100%;
    background: none;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.jumbotron .logo {
    width: 50px;
    height: 50px;
    margin-bottom: 50px
}

.jumbotron h2{
    font-size: 50px;
    font-weight: 700;
    color: var( --bYel );
}

.jumbotron a {
    background: none;
    border-radius: 25px;
    padding: 0.6em 1.5em;
    border: 2px solid var( --bYel );
    color: var( --bYel );
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    margin-top: 50px;
    display: inline-block;
}

.jumbotron a:hover {
    background: var( --bYel );
    color: var( --dBrown );
    padding: 0.6em 2em;
}

/* Media Queries */

/* Large Tablet Sizes */
@media (max-width: 991px) {
    .jumbotron h2 {
        font-size: 40px;
        font-weight: 700;
        color: var( --bYel );
    }
}

/* Medium Tablet Sizes */
@media (max-width: 767px) {
    .jumbotron h2 {
        font-size: 30px;
        font-weight: 700;
        color: var( --bYel );
    }
}

/* Phone Sizes */
@media (max-width: 479px) {
    .jumbotron h2 {
        font-size: 20px;
        font-weight: 700;
        color: var( --bYel );
    }
}
    
/* About Styles */

.About {
    width: 100%;
    height: 100%;
    padding-bottom: 100px;
}

.About .container .about-me {
    text-align: center
}

.About .container .about-me .img-me {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin: 50px 0;
}


.About .container .about-me p { 
    font-size: 24px;
    color: var( --dBrown );
    max-width: 570px;
    margin: 0 auto;
}

.About .container .about-me .my-skills { 
    margin-top: 100px
}

.About .container .about-me .my-skills h3 { 
    font-size: 24px;
    color: var( --dBrown ); 
    font-weight: 700;
}

.About .container .about-me .my-skills .skill-icon{
    margin: 20px 0;
}

.About .container .about-me .my-skills p {
    font-size: 14px;
    color: rgba(0, 0, 0, 0.7);
}
