.Projects .project-wrapper .project .project-desc {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(37,37,37,0.9);
    top: 100%;
    border-radius: inherit;
    padding: 1vw;
    text-align: center;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out
}

.Projects .project-wrapper .project:hover .project-desc{
    top: 0%;
    color: var( --bYel );
}

.Projects .project-wrapper .project .project-desc .project-name{
    font-weight: 700;
    font-size: 25px;
    margin-top: 50px;
}

.Projects .project-wrapper .project .project-desc p{
    font-size: 1.25em !important
}


.Projects .project-wrapper .project .project-desc a{
    background: none;
    border-radius: 14px;
    padding: 0.2em 1.5em;
    border: 2px solid var( --bYel );
    color: var( --bYel );
    font-weight: 500;
    text-decoration: none;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    margin-top: 1vw;
    display: inline-block;
}

.Projects .project-wrapper .project .project-desc a:hover{
    color: var( --dBrown );
    background: var( --bYel );
}

/* Media Queries */
@media (max-width: 1088px) {
    .container{
        width: 100%;
    }
    .col-sm-4 {
        width: 50%
    }
}

@media (max-width: 769px) {
    .col-sm-4 {
        width: 100%
    }

    .Projects .project {
        height: 400px;
    }

    .Projects .project-wrapper .project .project-desc .project-name {
        margin-top: 0;
    }

    .Projects .project-wrapper .project .project-desc .project-content { 
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        width: 90%

    }
}
/* Contact Styles */

.Contact {
    width: 100%;
    height: 600px;
    text-align: center;
    position: relative;
}

.Contact .container .row {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100%;
    margin: 0 auto;
}

.Contact .container h3 {
    font-size: 30px;
    font-weight: 700;
    color: var( --dBrown );
}

.Contact .container p {
    font-size: 20px;
    font-weight: 500;
    color: var( --dBrown );
}

.Contact .container a {
    background: none;
    border-radius: 25px;
    padding: 0.6em 1.5em;
    border: 2px solid var( --dBrown );
    color: var( --dBrown );
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    margin-top: 50px;
    display: inline-block;
}

.Contact .container a:hover {
    background: var( --dBrown );
    color: white;
    padding: 0.6em 2em;
}


/* Footer Styles */

.Footer { 
    width: 100%;
    background: var( --dBrown );
    text-align: center;
    padding: 50px 0;
}

.Footer .container .img-logo {
    width: 50px;
    height: 50px
}

.Footer .container .social-media {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    width: 100%;
    margin-top: 50px
}

.Footer .container .social-media .media-links {
    width: 40px;
    height: 40px;
    margin: 0px 10px;
    transition: transform 200ms cubic-bezier(0.25, 0.39, 0.39, 2.01), -webkit-transform 200ms cubic-bezier(0.25, 0.39, 0.39, 2.01);
    background: var( --bYel );
    border-radius: 50%;
}

.Footer .container .social-media .media-links:hover {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}

.Footer h3 {
    font-size: 20px;
    color: var( --bYel );
    font-weight: 600;
    padding: 30px 0px
}

.Footer hr {
    width: 200px;
    height: 2px;
    border: 0;
    background: var( --bYel );
    margin: 0 auto
}

.Footer h5 { 
    color: var( --bYel );
    margin: 40px 0px 0px 0px;
}
